@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: #1a1a1c;
  color: #fff;
}

main{
  width: 1120px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
}

header{
  width: 100%;
   padding: 20px 0 0;
}
header .logo img{
  width: 80px;
  display: none;
} 
header .logo figure{
 display: flex;
 align-items: center;
 font-size: 40px;
 font-weight: 700;
}
header nav {
  margin: 20px auto;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
}

header nav a {
  text-decoration:  none;
  font-size: 20px;
}

.toggle-nav{
  display: none;
}
.page-header{
   text-align: center;
    margin: 30px 0;
    letter-spacing: 2px;
    font-size: 30px;
    display: none;
}

/* Crypto-Index Page */
/* Loader */
.loader-wrapper{
  position: absolute;
  background-color: #1a1a1c;
  width: 100%;
  display: flex;
  justify-content: center;
  min-height: 100vh;
  padding: 80px 0 0;
}
.loader {
  display: inline-block;
  width: 50px;
  height: 50px;
  border: 3px solid rgba(255,255,255,.3);
  border-radius: 50%;
  border-top-color: #fff;
  animation: spin 1s ease-in-out infinite;
  -webkit-animation: spin 1s ease-in-out infinite;
}


/* Loader End */

.crypto-chart, .crypto-news{
  position: relative;
  width: 100%;
  margin-top: 60px;
}
.crypto-search {
  margin: 25px;
  width: 900px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}

.crypto-search h1 {
  margin-bottom: 32px;
  text-align: center;
}

.search-input {
  padding-left: 16px;
  width: 250px;
  height: 50px;
  border-radius: 4px;
  border: none;
  /* background-image: linear-gradient(
    -225deg,
    #ac32e4 0%,
    #7918f2 48%,
    #4801ff 100%
  ); */
  background-image: linear-gradient( 
    -225deg, #f79323 0%, #675019 48%, #3f2719 100% );
  color: #e2e2e2;
  font-size: 19px;
}

.search-input::-webkit-input-placeholder {
  color: #e2e2e2;
}

.search-input:-ms-input-placeholder {
  color: #e2e2e2;
}

.search-input::placeholder {
  color: #e2e2e2;
}


/* Currency CSS */

.currency-container{
  display: flex;
  justify-content: center;
}

.chart-row{
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  height: 80px;
  border-bottom: 1px solid #d7d7d7;
  width: 100%;
}
.chart-row.chart-header {
  font-size: 19px;
}
.chart-row.chart-header p {
  font-weight: 700;
  letter-spacing: 1px;
}
.coin{
  display: flex;
  align-items:  center;
  padding-right: 20px;
  min-width: 270px;

}

.coin h2{
  font-size: 18px;
  line-height: 25px;
  letter-spacing: 1px;
}

.coin img{
  height: 40px;
  width: 40px;
  margin-right: 10px;
}
.coin-symbol{
    text-transform: uppercase;
    padding: 0 10px;
}

.coin-data{
  display: flex;
  text-align: center;
  justify-content:  space-between;
  align-items: center;
}

.coin-price{
  font-size: 21px;
  width: 160px;
  text-align: right;
  padding-right: 20px;
  letter-spacing: 1.4px;
}
.coin-marketcap, .coin-volume, .circ-supply{
  opacity: 0.9;
}
.coin-volume{
  width: 160px;
  text-align: right;
}
.circ-supply{
  width: 240px;
  text-align: right;
}

.coin-marketcap {
  width: 190px;
  letter-spacing: 1px;
}
.coin-percent{
    width: 100px;
    font-weight: 700;
    font-size: 18px;
}
p.coin-percent span {
  display: none;
}
.red{
    color: red;
}

.green{
    color: green;
}

/* News Page */
.news-feed a, .news-feed a *{
  text-decoration: none;
}
.news-item{
  display: flex;
  margin: 20px 0;
}
.news-item figure{
  display: block;
  width: 300px;
  min-height: 160px;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
} 
.news-item img {
  width: 300px;
  max-height: 200px;
}
.news-content{
  padding: 10px 20px;
}
.news-content h3{
  font-size: 28px;
}
.news-content p {
  padding-top: 10px;
  font-size: 16px;
}


/* Animation for Loader */
@keyframes spin {
  to { -webkit-transform: rotate(360deg); }
}
@-webkit-keyframes spin {
  to { -webkit-transform: rotate(360deg); }
}

/* Bitcoin Logo Animation */
#bitcoin{
  display:block;
  text-align:center;
  color:#888888;
  margin-right: 20px;
}

.path {
  stroke-dasharray: 2110;
  stroke-dashoffset: 2110;
  stroke-width:5;
  stroke-linecap:round;
  -webkit-animation: dash 6s linear infinite;
          animation: dash 6s linear infinite;
  fill-opacity:0;
  stroke:orange;
}

@-webkit-keyframes dash {
  0% {
    stroke-dashoffset: 2110;
    opacity:0;
    stroke:orange;
  }
  15%{
    opacity:1;
    stroke:orange;
  }
  70%{
    opacity:1;
    stroke:orange;
  }
  100%{
    stroke-dashoffset:0;
    opacity:0;
    stroke:yellow;
  }
}

@keyframes dash {
  0% {
    stroke-dashoffset: 2110;
    opacity:0;
    stroke:orange;
  }
  15%{
    opacity:1;
    stroke:orange;
  }
  70%{
    opacity:1;
    stroke:orange;
  }
  100%{
    stroke-dashoffset:0;
    opacity:0;
    stroke:yellow;
  }
}


@media only screen and (max-width: 641px) { 
  header{
    padding-top: 0;
  }
  .logo {
    width: 100%;
    position: fixed;
    height: 80px;
    z-index: 1;
    }
  header nav {
    display: none;
    position: fixed;
    z-index: 111111;
    height: 100vh;
    width: 100vw;
    padding: 0 20px;
    flex-direction: column;
    justify-content: flex-start;
}
header nav.open {
  display: flex!important;
  margin-top: 80px;
}
  header nav a {
    font-size: 30px;
    height: 70px;
    line-height: 70px;
}
  figcaption {
    width: 100%;
    text-align: center;
    font-size: 28px;
    line-height: 80px;
  }
  header .logo img {
    width: 80px;
    padding: 10px;
}
  #bitcoin{
    margin-right: 5px;
    padding: 10px;
    position: absolute;
    top: 0;
  }
  svg#Layer_1 {
      width: 60px;
      height: 60px;
  }
   
  main {
      width: 100%;
  }
  .crypto-chart, .crypto-news {
      margin-top: 20px;
  }
  section.crypto-index {
      width: 94%;
      margin: 0 auto;
      padding-top: 80px;
  }
  .coin{
    padding-right: 0;
  }

  .coin h2 {
    font-size: 20px;
    padding-left: 10px;
  }
  .coin-data p {
    padding:   7px 0 0;
  }
  .currency-container{
    height: 110px;
  }
  .chart-row.chart-header {
    border-bottom: none;
  }
  .chart-row.chart-header .coin-data{
    display:none;
  }
  #first-currency-container{
    height: 90px;
  }
  .chart-row {
    flex-direction: column;
    height: 100px;
  }
  .coin-data {
    flex-direction: column;
    text-align: unset;
    flex-wrap: wrap;
  }
  .coin img {
    height: 50px;
    width: 50px;  
}
  .chart-row * {
      width: 100%;
  }
  .coin-symbol {
    text-align: right;
    font-size: 21px;
    padding-right: 0;
  }
  .coin-percent {
    width: 150px;
    font-size: 22px;
    position: absolute;
    left: 70px;
}
  p.coin-percent span {
    font-size: 17px;
    padding-left: 8px;
    opacity: 0.8;
    text-align: right;
    line-height: 22px;
    display: inline;
    display: initial;
  }
  .coin-marketcap {
    width: 160px;
    position: absolute;
    left: 20px;
    bottom: 8px;
    display: none;
}
  .coin-volume, .circ-supply{
    display: none;
  }
  
  .toggle-nav {
    position: fixed;
    top: 14px;
    right: 13px;
    z-index: 11;
    display: block;
    width: 46px;
    height: 50px;
    cursor: pointer;
    background: #1a1a1c;
  }
  .toggle-nav span,
  .toggle-nav span:before,
  .toggle-nav span:after {
    content: '';
    position: absolute;
    left: 0;
    display: block;
    text-indent: -9999px;
    background-color: #fff;
    height: 3px;
    width: 100%;
    transition: background-color 0.3s ease-in-out, top 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
    transition: background-color 0.3s ease-in-out, transform 0.5s ease-in-out, top 0.5s ease-in-out;
    transition: background-color 0.3s ease-in-out, transform 0.5s ease-in-out, top 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
  }
  .toggle-nav span {
    top: 10px;
    width: 40px;
  }
  .toggle-nav span:before {
    top: 14px;
  }
  .toggle-nav span:after {
    top: 28px;
  }
  .toggle-nav.open  span {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    top: 25px;
  }
  .toggle-nav.open span:before {
    background-color: transparent;
  }
  .toggle-nav.open span:after {
    top: 0;
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
  }
} 
